<template>
    <div class="flex flex-wrap justify-center gap-4 text-sm py-6 m-auto
    md:text-2xl md:w-4/6">
    <template v-if="!isMobile">
        <button v-for="(item, index) in poles" :key="index"
            :class="{'btn-active': (item.active)}"
            class="border-2 border-blue_1 bg-white text-blue_1 px-8 hover:border-orange_1 hover:bg-orange_1 hover:text-white"
            @click="getPoleInfo(item.id)">{{item.name}}</button>        
    </template>    
    <template v-if="isMobile">
        <Multiselect
            v-model="selectedPole" 
            mode="single"
            :searchable="true"                                               
            :options="poleListFormat"
            placeholder="Seleccione destino"      
            :classes="select_style_opt"
            @select="handleSelect"
            />
        <!-- <model-select :options="poleListFormat" 
            v-model="selectedPole"
            placeholder="Seleccione destino"
            class="search-select border border-blue_1 rounded-4xl text-dark_gray_1 w-3/4 text-xl "
            >
        </model-select> -->
    </template>
    </div>
</template>
<script>
import Multiselect from '@vueform/multiselect'
import '@vueform/multiselect/themes/default.css'

export default {
    name: 'PoleList',
    props: {
        poles: [],        
    },
    components: {
        Multiselect
    },
    data() {
        return {   
            select_style_opt: {
                container: 'relative mx-auto w-10/12 flex items-center justify-end box-border cursor-pointer border border-blue_1 bg-white text-xl text-blue_1 leading-snug outline-none',
                search: 'w-10/12 absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-base text-blue_1 font-sans bg-white pl-2 rtl:pl-0 rtl:pr-3.5',                                                        
                optionSelected: 'text-white bg-light_gray_1'
            },         
            selectedPole: {
                label: 'La Habana',
                value: 1
            }
        }
    },       
    methods: {        
        getPoleInfo(poleId) {            
            this.$emit('poleInfo', poleId)
        },
        handleSelect(pole_id) {
            if(pole_id != this.selectedPole.value)
                this.getPoleInfo(pole_id)
        }
    },
    computed: {
        isMobile() {
            return window.innerWidth <= 991
        },
        poleListFormat() {
            const formated_poles = []
            this.poles.map(pole => {
                formated_poles.push({label: pole.name, value: pole.id})
            })
            // this.selectedPole = formated_poles[0]
            return formated_poles
        }        
    }
}
</script>
<style scoped>
    .btn-active{
      border-color: var(--orange_1) !important;
      background-color: var(--orange_1) !important;
      color: white !important;
   }
</style>