<template>
    <component
        :is="ProductFilter"
        :poles="poles"
        :params="{
            poleKey: country,
            productKey: product
        }"
    ></component>
</template>

<script>

import HotelFilter from './HotelFilter.vue'
import CarFilter from './CarFilter.vue'

export default {
   name: 'ProductFilter',
   components: {
    CarFilter,
    HotelFilter
   },
   props:{
    product: String,
    country: String,
    poles: Array,
   },
   data(){
    return {
        ProductFilter: '',        
    }
   },
   watch: {
       product(newProduct) {
           switch (newProduct) {
               case 'car':
                   this.ProductFilter = 'CarFilter';
                   break;
                case 'hotel':            
                   this.ProductFilter =  'HotelFilter';
                   break;
           }    
       }
   }
}
</script>