<template>
   <div>
      <section class="bg-white">
         <h2 class="text-3xl pt-6 text-blue_1 
        md:text-6xl
        lg:text-4xl">- {{ product }} -</h2>

         <span class="font-Clarissa text-5xl">{{ country }}</span>

         <!-- BUSCADOR -->
         <searcher />
      </section>

      <section class="bg-white text-white">

         <div class="bg-blue_1 pb-10">

            <product-filter :country="country" :product="product" :poles="poles"
               @poleInfo="getPoleInfo" />

            <!-- Listado de productos por polo -->
            <div class="container flex flex-col gap-20 items-center py-10 m-auto">
               <span class="text-3xl" v-if="loading">
                  <i class="fa fa-spinner fa-pulse"></i>
               </span>
               <span class="text-3xl" v-if="noproduct">
                  No hay productos disponibles
               </span>

               <component :is="ProductListTemplate" v-for="(item, index) in product_list" :key="index" :item="item"
                  :index="index" :params="{
                     poleKey: country,
                     productKey: product
                  }"></component>

            </div>
         </div>
      </section>
   </div>
</template>
<script>
import Searcher from "@/components/searcher/Searcher.vue";
import PoleList from '@/components/product/PoleList.vue';

import HotelListTemplate from '@/components/product/HotelListTemplate.vue';
import CarListTemplate from '@/components/product/CarListTemplate.vue';

import ProductFilter from '@/components/Filter/ProductFilter.vue';

import PoleService from '@/services/common/pole.service';
import Authorization from '@/services/user/auth.service';

export default {
   name: 'ProductsListWrapper',
   components: {
      Searcher,
      PoleList,
      ProductFilter,
   },
   data() {
      return {
         product: '',
         country: '',
         loading: false,
         noproduct: false,
         poles: [],
         product_list: [],
         pole: ''
      }
   },
   created() {
      PoleService.getPolesByCountry(51).then(p => {
         this.poles = p
         this.poles[0].active = true
         this.search(this.poles[0].id)
         this.pole = this.poles[0].name
         this.product = this.$route.params.productKey
         this.country = this.$route.params.poleKey
      })
   },
   computed: {
      ProductListTemplate() {
         switch (this.$route.params.productKey) {
            case 'car':
               return CarListTemplate;
            case 'hotel':
               return HotelListTemplate;
            default:
               return CarListTemplate;
         }
      }

   },
   methods: {

      getPoleInfo(pole_id) {
         this.nohotel = false
         this.loading = false
         this.product_list = []

         this.poles.map(p => {
            p.active = (p.id == pole_id) ? true : false
         })

         this.search(pole_id)
      },
      search(pole_id) {
         // Get Hotel with price from by pole         
         // Searching by params         
         this.loading = true
         if (this.$route.params.productKey === 'car')
            this.getCar()

         if (this.$route.params.productKey === 'hotel')
            this.getHotel(pole_id)


      },
      handleProductFilter(list) {
         this.product_list = list
      },
      getImage(product_list) {
         for (let p of product_list) {
            if (p.images) {
               const img = this.getMainImage(p.images)
               if (img.length > 0)
                  p.image = img[0].url
            }
         }

      },
      // devualve la url de la imagen principal
      getMainImage(img_list, filter = 'original') {
         const main = img_list.filter(item => item.order === 1);
         if (main.length > 0) {
            const img = main[0].resources.filter(item => item.image === filter)
            if (img)
               return img
         }
         return []
      },
      getCar() {
         Authorization.checkLogin(`/car`).then(e => {
            this.loading = false
            if (e) {
               if (e.data) {

                  this.product_list = e.data
                  this.product_list.map(p => {
                     p.image = require("@/assets/img/car/car.jpg")
                  })
                  for (let p of this.product_list) {
                     if (p.images) {
                        const img = this.getMainImage(p.images)
                        if (img.length > 0)
                           p.image = img[0].url
                     }
                  }
                  sessionStorage.setItem('cars', JSON.stringify(this.product_list));
               } else {
                  this.noproduct = true
               }

            }
         })
      },
      getHotel(pole_id) {
         Authorization.checkLogin(`/hotels/by-pole/${pole_id}`).then(e => {
            this.loading = false
            if (e) {
               if (e.data) {
                  this.product_list = e.data
                  this.product_list.map(p => {
                     p.image = require("@/assets/img/hotel_thumb/thumb1.png")
                     p.segment = {
                        name: "playa",
                        img: require("@/assets/img/segment/playa.png")
                     }
                  })
                  for (let p of this.product_list) {
                     if (p.images) {
                        const img = this.getMainImage(p.images)
                        if (img.length > 0)
                           p.image = img[0].url
                     }
                  }
               } else {
                  this.nohotel = true
               }
            }
         })
      },
   }

}
</script>
<style scoped>
article {
   grid-template-rows: auto auto;
   grid-template-columns: auto;
}

@media screen and (min-width: 768px) {
   article {
      grid-template-columns: 27% auto;
      grid-template-rows: auto;
   }
}
</style>